import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { get } from "lodash";
import { WEATHER_URL } from "../constants/url";

const initialState = {
  location: undefined,
  data: {},
  loading: false,
};

export const fetchWeatherData = createAsyncThunk(
  "weather/fetchWeatherData",
  async (_, { dispatch, getState }) => {
    const state = getState();
    const location = get(state, "weather.location", "new delhi");

    return fetch(`${WEATHER_URL.FORECAST}&q=${location}&aqi=yes&days=7`).then(
      (res) => res.json()
    );
  }
);

const weatherReportSlice = createSlice({
  name: "weather",
  initialState,
  reducers: {
    setLocation(state, action) {
      state.location = action.payload;
    },
    setWeatherData(state, action) {
      state.loading = false;
      state.data = action.payload;
    },
  },
  extraReducers: {
    [fetchWeatherData.pending]: (state, action) => {
      state.loading = true;
    },
    [fetchWeatherData.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.data = payload;
    },
    [fetchWeatherData.rejected]: (state, { payload }) => {
      state.loading = false;
    },
  },
});

export const weatherReportActions = weatherReportSlice.actions;
export default weatherReportSlice.reducer;
